:root {
  /* Fallbacks */
  --accent: #007bff;
  --section-bg: #ffffff;
  --card-bg: #ffffff;
  --dynamic-text: #333333;
}

body { 
  margin: 0;
  transition: background-color 0.5s ease, color 0.5s ease;
  background-color: var(--section-bg);
  color: var(--dynamic-text);
}

/* --- NAVIGATION --- */
nav { 
  position: fixed; 
  top: 0; 
  width: 100%; 
  padding: 25px 0;
  text-align: center; 
  z-index: 1000;
  transition: all 0.4s ease;
}

nav.scrolled {
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  padding: 15px 0;
}

nav a { 
  color: white; 
  text-decoration: none; 
  margin: 0 15px; 
  font-weight: 600;
  text-transform: uppercase;
}

nav a:hover { color: var(--accent); }

/* --- HERO --- */
header { 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-position: center;
  transition: background-image 0.8s ease-in-out;
  color: white; /* Header text usually stays white for contrast */
}

.profile-circle {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  border: 4px solid white;
  margin-bottom: 20px;
}

/* --- DYNAMIC SECTIONS --- */
section { 
  padding: 100px 20px; 
  max-width: 1000px; 
  margin: auto;
  background-color: var(--section-bg); /* Changes with theme */
  color: var(--dynamic-text);          /* Changes with theme */
  transition: all 0.5s ease;
}

.card { 
  padding: 30px; 
  border-radius: 15px; 
  background-color: var(--card-bg);   /* Changes with theme */
  color: var(--dynamic-text);         /* Changes with theme */
  border: 1px solid rgba(0,0,0,0.1);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: all 0.5s ease;
}

.accent-link { 
  color: var(--accent); 
  text-decoration: none; 
  font-weight: bold; 
}

/* --- BUTTONS --- */
.btn-primary { 
  background: var(--accent); 
  color: white; 
}

.change-bg-btn {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid white;
  padding: 10px 20px;
  border-radius: 30px;
  cursor: pointer;
  margin: 20px 0;
  backdrop-filter: blur(5px);
}

.change-bg-btn:hover {
  background: var(--accent);
  border-color: var(--accent);
}

footer { 
  text-align: center; 
  padding: 50px; 
  background: #111; 
  color: #777; 
}